<template>
  <div>
    <!-- wrap -->
    <div>
      <mobile-header
        title="자주 묻는 질문"
        left-icon="mdi-chevron-left"
        class="mb-0"
        style="box-shadow: 0px 2px 14px rgba(0, 0, 0, 0.05)"
        @close="backRouting"
      />

      <div id="m_admin-main__page" class="pt-0">
        <section class="sidebar-items__wraps notice">
          <div class="title-box">
            <h4 class="mb-0">전체 목록</h4>
          </div>
          <ul class="preview-box ma-0 pa-0">
            <li v-for="(faqData, id) in getFaqList" :key="id" class="py-1">
              <div>
                <div
                  class="px-2"
                  style="display: flex; justify-content: space-between"
                  @click="faqDetail(faqData.id)"
                >
                  <span
                    style="font-weight: 500; font-size: 16px; color: #3f3531"
                    >{{ faqData.faqTitle }}</span
                  >
                  <v-icon>{{
                    faqDetailFlag === faqData.id
                      ? "mdi-chevron-up"
                      : "mdi-chevron-down"
                  }}</v-icon>
                </div>
                <v-divider class="mt-2"></v-divider>
                <div
                  v-if="faqDetailFlag === faqData.id"
                  style="background: #f8f6f5; padding: 24px 38px"
                >
                  <p
                    style="
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 170%;
                    color: #3f3531;
                  "
                  >
                    {{ faqData.faqContent }}
                  </p>
                </div>
              </div>
            </li>
          </ul>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
import myMixin from "@/mixin/mixin";
import dateFomat from "@/mixin/dateFomat";

import { mapGetters } from "vuex";
import MobileHeader from "@/components/common/MobileHeader.vue";

export default {
  name: "Faq",
  mixins: [myMixin, dateFomat],
  components: {
    MobileHeader
  },
  props: {},
  data() {
    return {
      faq: {
        id: null,
        faqTitle: this.$route.query.faqTitle ? this.$route.query.faqTitle : "",
        faqContent: this.$route.query.faqContent
          ? this.$route.query.faqContent
          : "",
        faqType: {
          codeId: this.$route.query.faqType ? this.$route.query.faqType : null
        },
        page: {
          page: this.$route.query.page ? this.$route.query.page : 0,
          size: this.$route.query.size ? this.$route.query.size : 5,
          sort: this.$route.query.sort ? this.$route.query.sort : "regDate,desc"
        }
      },
      faqDetailFlag: false,
      pages: function() {
        const list = [];
        for (let i = this.startPage; i <= this.endPage; i += 1) list.push(i);
        return list;
      }
    };
  },
  computed: {
    ...mapGetters([
      "getFaqList",
      "getFaqPage",
      "getFaqPageTotal",
      "getFaqTotalElements"
    ]),
    startPage() {
      return parseInt((this.getFaqPage - 1) / 5) * 5 + 1;
    },
    endPage() {
      return (parseInt((this.getFaqPage - 1) / 5) + 1) * 5 <
        this.getFaqPageTotal
        ? (parseInt((this.getFaqPage - 1) / 5) + 1) * 5
        : this.getFaqPageTotal;
    }
  },
  mounted() {
    this.requestGetFaqList();
  },
  beforeDestroy() {
    this.$store.commit("FAQ_INIT");
  },
  updated() {
    var $subTopMenu = $(".faq_list .q_btn");
    $subTopMenu.unbind("click");
    $subTopMenu.on("click", function() {
      var index = $(this).index();
      if (
        $(this)
          .parent()
          .hasClass("active")
      ) {
        $(this)
          .parent()
          .removeClass("active");
      } else {
        $(this)
          .parent()
          .addClass("active");
      }
      return false;
    });
  },
  methods: {
    requestGetFaqList() {
      const parameter = {
        faq: this.faq
      };
      this.$store.dispatch("REQUEST_FAQ_LIST", parameter);
    },
    onPageChange(val) {
      const param = {
        page: val
      };
      this.requestFaqList(param);
    },
    requestFaqList(param) {
      let currentPage = 0;
      if (
        param !== undefined &&
        param !== null &&
        param.page !== undefined &&
        param.page !== null
      ) {
        currentPage = param.page - 1;
      }
      this.$router.push({
        path: "/faq",
        query: {
          page: currentPage,
          size: this.faq.page.size,
          sort: this.faq.page.sort
        }
      });
    },
    backRouting() {
      this.$router.push("/");
    },

    faqDetail(id) {
      if (this.faqDetailFlag === id) {
        this.faqDetailFlag = null;
        return;
      }
      this.faqDetailFlag = id;
    }
  }
};
</script>
