import moment from "moment";

const emptyDate = () => {
  return "";
};

const dateFomat = {
  methods: {
    YYMMDDSLASHDate(val) {
      if (!val) {
        return emptyDate();
      }

      return moment(val).format("yyyy-MM-DD");
    },
    YYSLASHDate(val) {
      if (!val) {
        return emptyDate();
      }

      return moment(val).format("MM/DD HH:mm");
    },
    YYMMDDDate(val) {
      if (!val) {
        return emptyDate();
      }

      return moment(val).format("YY.MM.DD");
    },
    YYYYMMDDDate(val) {
      if (!val) {
        return emptyDate();
      }

      return moment(val).format("yyyy.MM.DD");
    },
    YYYYMMDDHHMMSSDate(val) {
      if (!val) {
        return emptyDate();
      }

      return moment(val).format("yyyy.MM.DD HH:mm:ss");
    },
    YYYYDate(val) {
      if (!val) {
        return emptyDate();
      }

      return moment(val).format("yyyy");
    },
    MMDate(val) {
      if (!val) {
        return emptyDate();
      }

      return moment(val).format("MM");
    },
    DDDate(val) {
      if (!val) {
        return emptyDate();
      }

      return moment(val).format("DD");
    },
    YYMMDDHHmmDate(val) {
      if (!val) {
        return emptyDate();
      }

      return moment(val).format("MM/DD HH:mm");
    },
    YYMMDDHHmm2Date(val) {
      if (!val) {
        return emptyDate();
      }

      return moment(val).format("MM-DD HH:mm");
    },
    YYYYMMDDHHmmDate(val) {
      if (!val) {
        return emptyDate();
      }

      return moment(val).format("yyyy-MM-DD HH:mm");
    },
    MMDDDate(val) {
      if (!val) {
        return emptyDate();
      }

      return moment(val).format("MM.DD");
    },
    KOREAYYYYMMDDEEHHMMDate(val) {
      if (!val) {
        return emptyDate();
      }

      const koreaE = ["(일)", "(월)", "(화)", "(수)", "(목)", "(금)", "(토)"];
      return (
        moment(val).format("yyyy년 MM월 DD일 ") +
        koreaE[moment(val).format("E")] +
        moment(val).format(" HH시 mm분")
      );
    },
    DateDiff(val) {
      let now = new Date();
      let diffNow = new Date(val);
      now = new Date(now.getFullYear(), now.getMonth() + 1, now.getDate());
      diffNow = new Date(
        diffNow.getFullYear(),
        diffNow.getMonth() + 1,
        diffNow.getDate()
      );
      let diff = Math.abs(now.getTime() - diffNow.getTime());
      diff = Math.ceil(diff / (1000 * 3600 * 24));
      return diff <= 3;
    },

    PRINTYYMMDDDate(val) {
      if (val === null || val === undefined || val === "") {
        return "";
      }
      const moment = require("moment");
      return moment(val).format("yyyy년 MM월 DD일");
    },
    PRINT2YYMMDDDate(val) {
      if (val === null || val === undefined || val === "") {
        return "";
      }
      const moment = require("moment");
      moment.lang("ko", {
        weekdays: [
          "일요일",
          "월요일",
          "화요일",
          "수요일",
          "목요일",
          "금요일",
          "토요일"
        ],
        weekdaysShort: ["일", "월", "화", "수", "목", "금", "토"]
      });
      return moment(val).format("yyyy년 MM월 DD일 (dddd) A hh:mm");
    }
  }
};

export default dateFomat;
